"use client"

import Link from "next/link"
import { Button } from "@/components/ui/button"
import { Sparkles } from "lucide-react"
import { usePathname } from "next/navigation"

export function Header() {
  const pathname = usePathname()

  const navItem = (path: string, label: string) => {
    const active = pathname === path

    return (
      <Link href={path}>
        <Button
          variant="ghost"
          className={`
            rounded-full px-5
            ${active ? "bg-gradient-to-r from-blue-600 to-purple-600 text-white" : ""}
            ${!active ? "text-gray-700" : ""}
            hover:bg-transparent
          `}
        >
          <span
            className={`
              ${active ? "" : "hover:bg-gradient-to-r hover:from-blue-600 hover:to-purple-600"}
              hover:bg-clip-text hover:text-transparent
              transition-all duration-300
            `}
          >
            {label}
          </span>
        </Button>
      </Link>
    )
  }

  return (
    <header className="sticky top-0 z-50 backdrop-blur-xl border-b border-blue-200/40
      bg-gradient-to-r from-white via-blue-50/80 to-indigo-100/70">

      <div className="container mx-auto px-6 py-4 flex items-center justify-between">

        {/* Logo */}
        <Link href="/" className="font-extrabold text-2xl bg-gradient-to-r from-blue-700 via-indigo-600 to-blue-800 bg-clip-text text-transparent">
          Harvard Learning
        </Link>

        {/* Navigation */}
        <nav className="hidden md:flex items-center gap-2">
          {navItem("/", "Home")}
          {navItem("/about", "About")}
          {navItem("/courses", "Courses")}
          {navItem("/persona", "Persona")}
        </nav>

        {/* CTA */}
        <Button className="bg-gradient-to-r from-indigo-600 via-blue-600 to-cyan-500 text-white rounded-full px-6 flex items-center gap-2">
          Enroll Now
          <Sparkles className="h-4 w-4" />
        </Button>

      </div>
    </header>
  )
}